import React from 'react';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import { Typography } from '@material-ui/core';
import { makeStyles  } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import James from './ca_james.jpg'
import Venkatakrishnan from './ca_venkat.jpg'
import Murali from './ca_murali.jpg'
import Padmanabha from './ca_padmanabha_pillai.jpg'
import Alex from './ca_alex.jpg'

const useStyles = makeStyles ({
    root: {
        maxWidth: 345
    },
    rootTyp: {
        paddingTop: '10px'
    }
});

function AboutUs() {
    const classes = useStyles();
    return (
        <Container maxWidth="lg" style={{'paddingBottom': '10px'}}>
            <Grid container direction="row">
                <Grid item xs={12}>
                    <Typography variant="h5" classes={{root: classes.rootTyp}}>
                        {"About Us"}
                    </Typography>
                    <Typography variant="body1" classes={{root: classes.rootTyp}}>
                        {"A P J M V & ASSOCIATES LLP is a Limited Liability Partnership (LLP) firm. It is registered with Registrar of Companies, Ernakulam on Sep 21, 2017 by 5 chartered accounts with rich experience in accounting, management and consulting fields. Main Business Activity of A P J M V & Associates LLP are Audit, Consultancy, Management Services and associated activities. The LLP has 3 Partners and 2 Designated Partners. The designated partners are: James Menachery and Venkatakrishnan Sivasailam."}
                    </Typography>
                    <Typography variant="h6" classes={{root: classes.rootTyp}}>
                        {"Founders"}
                    </Typography>
                </Grid>
            </Grid>
            <Grid container direction="row" spacing={6} style={{'paddingTop': '10px'}}>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="James"
                            image={James}
                            title="James"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. James Menachery"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Description
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Venkatakrishnan"
                            image={Venkatakrishnan}
                            title="Venkatakrishnan"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Venkatakrishnan S"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Description
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Murali"
                            image={Murali}
                            title="Murali"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Murali Nair V"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Description
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Padmanabha"
                            image={Padmanabha}
                            title="Padmanabha"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Padmanabha Pillai S"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Description
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
                <Grid item xs={12} sm>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                            component="img"
                            alt="Alex"
                            image={Alex}
                            title="Alex"
                            />
                            <CardContent>
                            <Typography gutterBottom variant="subtitle2" component="p">
                                {"CA. Alex Jose"}
                            </Typography>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Description
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    );
}

export default AboutUs;